import React,{useState,useEffect} from 'react'
import axios from 'axios'
import { useParams } from 'react-router';
import back from "../images/EVmz8acUEAEmWeX.jpg"
import "./profile.css"
import Header from '../components/Header';
import { useNavigate } from 'react-router-dom';

export default function Profile(props) {
    const [data, setdata] = useState({});
    const [loaddata, setloaddata] = useState(false);
    const [notfound, setnotfound] = useState(false);
    const params = useParams();
    useEffect(() => {
    axios.get(`http://herostest.somee.com/api/SuperHeros/${params.id}`)
    .then(res => {
        setdata(res.data);
        setloaddata(true);
    }).catch((e)=>{
        if(e.toJSON().status == 404)
        {
            setnotfound(true);
        }
    });
    },[]);

    let navigate = useNavigate();

    const delehandle=async()=>{
        await axios.delete(`http://herostest.somee.com/api/SuperHeros/${params.id}`,data);
        navigate(process.env.PUBLIC_URL+`/heros`);
    }

    const edithandle=()=>{
        navigate(process.env.PUBLIC_URL+`/edithero?id=${params.id}`, {replace: true});
    }

    if(notfound)
    {
        return(
            <>
            <Header/>
            <img src={back} alt="back" className='back'/>
            <div className="profile">
                    Not Found
            </div>
            
        </>
        )
    }
    else{
        return (
            <>
                <Header/>
                <img src={back} alt="back" className='back'/>
                <div className="profile">
                        {loaddata?<img src={data.photoURL} alt="photos"/>:"loading"}
                        <div className="data">
                            <div className="top">
                                <h1>{loaddata?data.name:""}</h1>
                                <h3>{loaddata?data.realName:""} {loaddata?data.isbad?<span>bad</span>:"":""}</h3>
                                <p><i className="fas fa-bolt"></i> <span>Power:</span> {data.power}</p>
                                <p><i className="fas fa-chevron-up"></i> <span>PowerRange:</span> {data.powerRange}</p>
                                <p><i className="fas fa-shield-alt"></i> <span>Team:</span> {data.team}</p>
                            </div>
                            <div className="buttom">
                                <i className="fas fa-trash" onClick={delehandle}></i>
                                <i className="fas fa-user-edit" onClick={edithandle}></i>
                            </div>
                        </div>
                </div>
                
            </>
        )
    }
}
