import React,{useState,useEffect} from 'react'
import "./ranking.css"
import axios from 'axios'
import Ranksu from '../components/Ranksu'
import Header from "../components/Header"

export default function Ranking() {
    const [data, setdata] = useState([]);
    const [loaddata, setloaddata] = useState(false);

    useEffect(() => {
    axios.get(`http://herostest.somee.com/GetByPower`)
    .then(res => {
        setdata(res.data);
        setloaddata(true);
    })
    },[]);
  return (
    <>
    <Header/>
    <div className="container">
        <div className="rank">
        <div className="title underlinewhite white">Ranking</div>
            <div className="body">
                {loaddata?data.map((p,i)=><Ranksu key={i} name={p.name} powerrange={p.powerRange} img={p.photoURL} number={i}/>):"loading"}
            </div>
        </div>
    </div>
    </>
  )
}
