import React,{useState,useRef} from 'react'
import Header from '../components/Header'
import "./addhero.css"
import Swal from 'sweetalert2';
import axios from 'axios';

export default function AddHero() {
    const [name, setname] = useState("");
    const [realname, setrealname] = useState("");
    const [team, setteam] = useState("");
    const [power, setpower] = useState("");
    const [powerrange, setpowerrange] = useState("");
    const [bad, setbad] = useState("notbad");
    const nameinput = useRef(null);
    const realnameinput = useRef(null);
    const teaminput = useRef(null);
    const powerinput = useRef(null);
    const powerrangeinput = useRef(null);
    const isbadinput = useRef(null);
    const [URL, setURL] = useState("https://www.pngitem.com/pimgs/m/9-93862_my-account-account-vector-icon-png-transparent-png.png")

    const submitclick =async () =>{
            if(name.trim()!==""&&realname.trim()!==""&&team.trim()!==""&&power.trim()!==""&&powerrange.trim()!=="")
            {
                    // let hero ={
                    //     name:name,
                    //     realName:realname,
                    //     team:team,
                    //     power:power,
                    //     powerRange:powerrange,
                    //     isBad:bad==="bad",
                    //     photoURL:URL
                    // }

                    let hero={
                        "id": 0,
                        "name": name,
                        "realName": realname,
                        "photoURL": URL,
                        "power": power,
                        "powerRange": Number(powerrange),
                        "team": team,
                        "isbad": bad==="bad"
                      }
                    axios.post("http://herostest.somee.com/api/SuperHeros",hero);
                    nameinput.current.value="";
                    realnameinput.current.value="";
                    teaminput.current.value="";
                    powerinput.current.value="";
                    powerrangeinput.current.value="";
                    isbadinput.current.value="notbad";
                    setname("");
                    setrealname("");
                    setteam("");
                    setpower("");
                    setbad("");
                    setpowerrange("");
                    setURL("https://www.pngitem.com/pimgs/m/9-93862_my-account-account-vector-icon-png-transparent-png.png")
                    localStorage.setItem("form",true);
                    Swal.fire({
                        icon: 'success',
                        title: 'تم ارسال بياناتك بنجاح'
                    });
            }
            else{
                console.log(name);
                console.log(realname);
                console.log(team);
                console.log(power);
                console.log(bad);
                Swal.fire({
                    icon: 'error',
                    title: 'يرجي ملئ الحقول'
                });
            }
    }
    
    //upload photo
    const [barstyle, setbarstyle] = useState({
        display:"none"
      })
      const [progressstyle, setprogressstyle] = useState({
        width: "0"
      })
    
      const handleChange =async (e) =>{
        if (e.target.files[0]) {
          if(e.target.files[0]['type'].split('/')[0] === 'image'){
            if(e.target.files[0].size <=8388608)
            {
                let percent=0;
                const formData = new FormData();
                formData.append("file",e.target.files[0]);
                formData.append("upload_preset","hcgqvulf");
                const options={
                    onUploadProgress:(e)=>{
                        percent=Math.floor((e.loaded*100)/e.total);
                        console.log(percent)
                        setbarstyle({
                            display:"block"
                        });
                        setprogressstyle({
                        width:percent+"%"
                        })
                    }
                }
                await axios.post("https://api.cloudinary.com/v1_1/herostest/image/upload",formData,options).then((response)=>{
                    console.log(response);
                    setURL(response.data.url);
                })
                setbarstyle({
                    display:"none"
                });
            }
            else{
              Swal.fire({
                icon: 'error',
                title: 'يجب ان يكون حجم الصورة اقل من 8 ميجا',
                showConfirmButton: false,
                timer: 2000
            })
            } 
          }
          else{
            Swal.fire({
              icon: 'error',
              title: 'المدخل ليس صورة',
              showConfirmButton: false,
              timer: 2000
          })
          }
        }
      }
    
  return (
    <>
        <Header/>
        <div className="container">
            <div className="inputs">
                <div className="title underlinewhite white">Add Hero</div>
                <div className="theform" >
                    <div className="profilephoto">
                    <div className="bar" style={barstyle}>
                        <div className="progress" style={progressstyle}></div>
                    </div>
                    <div style={{"position":"relative","width":"min-content","margin":"auto"}}>
                    <img src={URL} alt="userphoto"/>
                    <input type="file" id="file" onChange={handleChange} />
                    <label htmlFor="file" >
                        <i className="fas fa-pen" ></i>
                    </label>
                    </div>
                </div>
                <div className="inputs">
                    <div>
                        <label>
                        Name:
                        <br/>
                        <input type="text" onChange={(e)=>setname(e.target.value)} ref={nameinput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        Real name:
                        <br/>
                        <input type="tel" onChange={(e)=>setrealname(e.target.value)} ref={realnameinput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        Team:
                        <br/>
                        <input type="tel" onChange={(e)=>setteam(e.target.value)} ref={teaminput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        Power:
                        <br/>
                        <input type="text" onChange={(e)=>setpower(e.target.value)} ref={powerinput}/>
                        </label>
                    </div>
                    <div>
                        <label>
                        Power Range:
                        <br/>
                        <input type="text" onChange={(e)=>setpowerrange(e.target.value)} ref={powerrangeinput}/>
                        </label>
                    </div>
                    <div style={{"display":"flex",
                                "justifyContent":"space-between"
                                }}>
                        <label>
                        isbad:
                        <br/>
                        <select onChange={(e)=>setbad(e.target.value)} ref={isbadinput}>
                            <option selected defaultValue="bad">bad</option>
                            <option defaultValue="notbad">notbad</option>
                        </select>
                        </label>
                        <button onClick={submitclick}>ارسال</button>
                    </div>
                </div>
            </div>
            </div>
        </div>
    </>
  )
}
