import React,{useState,useEffect} from 'react'
import Header from '../components/Header'
import "./heros.css"
import Hero from "../components/Hero"
import axios from 'axios';

export default function Heros() {
    const [data, setdata] = useState({})
    const [loaddata, setloaddata] = useState(false)

    useEffect(() => {
    axios.get(`http://herostest.somee.com/api/SuperHeros`)
    .then(res => {
        setdata(res.data);
        setloaddata(true);
    })
    },[])
    
  return (
    <>
        <Header/>
        <div className='container'>
            <div className="title underline">Heros</div>
            <div className="heros">
                {loaddata?data&&data.map((e,i)=><Hero photo={e.photoURL} power={e.power} powerrange={e.powerRange} name={e.name} realname={e.realName} key={i} id={e.id}/>):"Loading"}
            </div>
        </div>
    </>
  )
}
