import './App.css';
import Home from './screens/Home';
import {BrowserRouter , Route,Routes} from "react-router-dom";
import Heros from './screens/Heros';
import Profile from './screens/Profile';
import Ranking from './screens/ranking';
import AddHero from './screens/AddHero';
import EditHero from './screens/EditHero';

function App() {
  return (
  <BrowserRouter>
    <Routes>
      <Route path={process.env.PUBLIC_URL+`/`} exact element={<Home/>} />
      <Route path={process.env.PUBLIC_URL+`/heros/`} exact element={<Heros/>} />
      <Route path={process.env.PUBLIC_URL+`/heros/:id`} exact element={<Profile/>} />
      <Route path={process.env.PUBLIC_URL+`/ranking`} exact element={<Ranking/>} />
      <Route path={process.env.PUBLIC_URL+`/addhero`} exact element={<AddHero/>} />
      <Route path={process.env.PUBLIC_URL+`/edithero`} exact element={<EditHero/>} />
    </Routes>
  </BrowserRouter>
  );
}

export default App;
