import React from 'react'

export default function Ranksu(props) {
    return (
        <div className="ranksu">
            <div className="markz">
                {props.number+1}
            </div>
            <div className="userdetails">
                <img src={props.img} alt="img" />
                <div className="details">
                    <div className="name">
                        {props.name}
                    </div>
                    <div className="level">
                        Power Range:{props.powerrange}
                    </div>
                </div>
            </div>
        </div>
      )
}
